            <p v-if="rfpRequestData" class="text-sm text-gray-500 mt-2">
              This workflow is now ready to be used for the RFP: <strong>{{ rfpRequestData.title || rfpRequestData.rfp_title || 'Untitled RFP' }}</strong>
